import React, { useState } from "react";
import { ISale } from "../../libs/api/types";
import { APISDK } from "../../libs/api";

interface EditSaleModalProps {
  sale: ISale;
  onClose: () => void;
  onSave: () => void;
}

type ISaleForm = Omit<ISale, "start_date" | "end_date" | "created_at" | "updated_at"> & {
  start_date: string;
  end_date: string;
};

export function EditSaleModal({ sale, onClose, onSave }: EditSaleModalProps) {
  const [formData, setFormData] = useState<ISaleForm>({
    ...sale,
    start_date: new Date(sale.start_date).toISOString().split("T")[0],
    end_date: new Date(sale.end_date).toISOString().split("T")[0],
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const salePayload: ISale = {
        ...formData,
        start_date: new Date(formData.start_date),
        end_date: new Date(formData.end_date),
        image_url: formData.image_url.map((url) => url.trim()),
        product_ids: formData.product_ids.map((id) => id.trim()),
        category_ids: formData.category_ids.map((id) => id.trim()),
        subcategory_ids: formData.subcategory_ids.map((id) => id.trim()),
        meta_data: JSON.parse(formData.meta_data as unknown as string),
        created_at: sale.created_at,
        updated_at: new Date(),
      };

      if (salePayload.id) {
        await APISDK.getInstance().updateSale(salePayload.id, salePayload);
      } else {
        await APISDK.getInstance().createSale(salePayload);
      }
      onSave();
      onClose();
    } catch (error) {
      console.error("Failed to save sale:", error);
      alert("Invalid meta_data JSON.");
    }
  };

  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        backgroundColor: "rgba(0,0,0,0.6)",
        zIndex: 999,
        overflowY: "auto",
      }}
    >
      <div
        style={{
          backgroundColor: "#fff",
          padding: "30px",
          margin: "50px auto",
          width: "500px",
          borderRadius: "10px",
          boxShadow: "0 4px 20px rgba(0,0,0,0.2)",
        }}
      >
        <h2 style={{ marginBottom: "20px", color: "#333", fontWeight: "600" }}>
          {formData.id ? "Edit Sale" : "Create Sale"}
        </h2>
        <form onSubmit={handleSubmit}>
          {/* Text Inputs */}
          {[
            { label: "Name", name: "name", type: "text" },
            { label: "Description", name: "description", type: "text" },
            { label: "Discount (%)", name: "discount_percentage", type: "number" },
            { label: "Image URLs (comma separated)", name: "image_url", type: "text" },
            { label: "Product IDs (comma separated)", name: "product_ids", type: "text" },
            { label: "Category IDs (comma separated)", name: "category_ids", type: "text" },
            { label: "Subcategory IDs (comma separated)", name: "subcategory_ids", type: "text" },
          ].map(({ label, name, type }) => (
            <div key={name} style={{ marginBottom: "15px" }}>
              <label style={{ display: "block", marginBottom: "5px", color: "#444" }}>{label}</label>
              <input
                type={type}
                name={name}
                value={
                  Array.isArray((formData as any)[name])
                    ? (formData as any)[name].join(", ")
                    : (formData as any)[name]
                }
                onChange={handleChange}
                required={name === "name" || name === "discount_percentage"}
                style={{
                  width: "100%",
                  padding: "10px",
                  borderRadius: "6px",
                  border: "1px solid #ccc",
                  outlineColor: "#4CAF50",
                  color: "#333",
                }}
              />
            </div>
          ))}

          {/* Date Inputs */}
          {["start_date", "end_date"].map((name) => (
            <div key={name} style={{ marginBottom: "15px" }}>
              <label style={{ display: "block", marginBottom: "5px", color: "#444" }}>
                {name.replace("_", " ").toUpperCase()}:
              </label>
              <input
                type="date"
                name={name}
                value={(formData as any)[name]}
                onChange={handleChange}
                required
                style={{
                  width: "100%",
                  padding: "10px",
                  borderRadius: "6px",
                  border: "1px solid #ccc",
                  outlineColor: "#4CAF50",
                  color: "#333",
                }}
              />
            </div>
          ))}

          {/* Sale Type */}
          <div style={{ marginBottom: "15px" }}>
            <label style={{ display: "block", marginBottom: "5px", color: "#444" }}>Sale Type:</label>
            <select
              name="sale_type"
              value={formData.sale_type}
              onChange={handleChange}
              style={{
                width: "100%",
                padding: "10px",
                borderRadius: "6px",
                border: "1px solid #ccc",
                outlineColor: "#4CAF50",
                color: "#333",
              }}
            >
              <option value="product">Product</option>
              <option value="category">Category</option>
              <option value="subcategory">Subcategory</option>
            </select>
          </div>

          {/* Meta Data */}
          <div style={{ marginBottom: "20px" }}>
            <label style={{ display: "block", marginBottom: "5px", color: "#444" }}>Meta Data (JSON):</label>
            <textarea
              name="meta_data"
              value={JSON.stringify(formData.meta_data, null, 2)}
              onChange={handleChange}
              rows={5}
              style={{
                width: "100%",
                padding: "10px",
                borderRadius: "6px",
                border: "1px solid #ccc",
                outlineColor: "#4CAF50",
                color: "#333",
                fontFamily: "monospace",
              }}
            />
          </div>

          {/* Action Buttons */}
          <div style={{ textAlign: "right" }}>
            <button
              type="submit"
              style={{
                backgroundColor: "#4CAF50",
                color: "white",
                padding: "10px 20px",
                border: "none",
                borderRadius: "6px",
                cursor: "pointer",
                marginRight: "10px",
              }}
            >
              Save
            </button>
            <button
              type="button"
              onClick={onClose}
              style={{
                backgroundColor: "#f44336",
                color: "white",
                padding: "10px 20px",
                border: "none",
                borderRadius: "6px",
                cursor: "pointer",
              }}
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
